* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.content {
  height: 85vh;
}

/* .navbar {
  background-color: #0000008b !important;
} */

.register-page {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 90vh;
}

/* transaction page */
.filters {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px gray;
}
/* transaction page */

/* antd icons */
.switch-icons {
  border: 1px solid rgba(0, 0, 0, 0.677);
  border-radius: 5px;
  padding: 10px 15px;
}

.anticon svg{
  font-size: 20px;
  cursor: pointer;
}

.active-icon {
  color: black;
}

.inactive-icon {
  color: gray;
};
/* antd icons */
.analytics-container {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .progress-bar-container {
    display: flex;
    justify-content: center;
    margin-top: 20px;
  }
  
  .card {
    margin-bottom: 20px;
  }
  
  .card-header {
    font-size: 18px;
    font-weight: bold;
    background-color: #f5f5f5;
  }
  
  .text-success {
    color: green;
  }
  
  .text-danger {
    color: red;
  }
  
.register-page {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-color: #f5f5f5;
  }
  
  .register-page form {
    max-width: 400px;
    width: 100%;
    padding: 24px;
    background-color: #fff;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .register-page h1 {
    margin-bottom: 24px;
    text-align: center;
    font-size: 24px;
    font-weight: bold;
    color: #555;
  }
  
  .register-page input[type="text"],
  .register-page input[type="email"],
  .register-page input[type="password"] {
    border-radius: 4px;
    border: 1px solid #ccc;
    font-size: 16px;
    padding: 8px 12px;
    width: 100%;
    margin-bottom: 16px;
  }
  
  .register-page button {
    background-color: #4CAF50;
    border: none;
    color: white;
    padding: 12px 24px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    border-radius: 4px;
    cursor: pointer;
  }
  
  .register-page button:hover {
    background-color: #3e8e41;
  }
  
  .register-page a {
    color: #0077c2;
    font-size: 16px;
    text-decoration: none;
  }
  
  .register-page a:hover {
    text-decoration: underline;
  }
  
